.wrapper {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 400px;
	background: #161616;
}

.list {
	width: 600px;
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 10px;
	padding: 10px 60px;
	background: #242424;
	border-radius: 30px;
}

.list li {
	list-style-type: none;
}

.list li button {
	position: relative;
	text-decoration: none;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80px;
	height: 80px;
	z-index: 1000;
	color: #444;
	background: none;
	border: none;
	outline: none;
	cursor: pointer;
}

.svg {
	width: 32px;
	height: 32px;
	transition: 0.5s;
	pointer-events: none;
}

.list li:hover .svg {
	color: #c7f80d;
	filter: drop-shadow(0 0 15px #c7f80d);
}

.active .svg {
	color: #c7f80d;
	filter: drop-shadow(0 0 15px #c7f80d);
	transform: translateY(-50px);
}

#marker {
	position: absolute;
	top: -40px;
	width: 80px;
	height: 80px;
	background: #55650a;
	border-radius: 20px;
	border-top: 2px solid #c7f80d;
	border-left: 2px solid #c7f80d;
	box-shadow: 0 0 0 10px #161616, 0 2px 0 9px #c7f80d;
	transition: 0.3s;
}

#marker::before {
	content: '';
	position: absolute;
	top: 39px;
	left: -30px;
	width: 20px;
	height: 20px;
	background: transparent;
	border-top-right-radius: 20px;
	box-shadow: 4px -4px 0 4px #161616;
}

#marker::after {
	content: '';
	position: absolute;
	top: 39px;
	right: -30px;
	width: 20px;
	height: 20px;
	background: transparent;
	border-top-left-radius: 20px;
	box-shadow: -4px -4px 0 4px #161616;
}

#marker span {
	position: absolute;
	top: 125px;
	width: 120%;
	height: 30px;
	left: -10%;
	background: #55650a;
	box-shadow: 0 0 0 10px #161616, 0 -2px 0 9px #c7f80d;
	border-radius: 20px;
	border-right: 1px solid #c7f80d;
	border-bottom: 1px solid #c7f80d;
}

.list p {
	position: absolute;
	/* bottom: 0; */
	pointer-events: none;
	transform: translateY(50px);
	opacity: 0;
	font-size: 0.8em;
	transition: 0.5s;
	text-transform: uppercase;
}

.list li.active p{
	opacity: 1;
	color: #c7f80d;
	filter: drop-shadow(0 0 10px #c7f80d);
}

